body {
    padding: 1em;
}

/* style={{backgroundColor: "red", position: "fixed", top: "0", left:"0", bottom: "0"}} */
div.sidebar {
    background-color: rgba(0, 0, 0, 0.03);
    border-right: 1px solid rgba(0, 0, 0, 0.125);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 15em;
}

div.sidebar a.active {
    background-color: rgba(0, 0, 0, 0.1);
}

.a {
    cursor: pointer;
}

.card-body {
    padding: 0.55em 0.75em;
}

.card-footer {
    padding: 0.6em;
}

.card-text {
    margin-bottom: 0.25em;
}